
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import DatetimeView from 'vux/src/components/datetime-view/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'

import axios from 'axios'
import * as util from '../api/common'

export default {
directives: {
TransferDom
},
components: {
Cell, Group, Tab, TabItem, DatetimeView, Popup, TransferDom, PopupHeader
},
data () {
return {
showStartPopup: false,
showEndPopup: false,
startTime: '选择开始时间',
endTime: '选择结束时间',
startTimeView: '',
endTimeView: '',
status: '',
list: []
}
},
mounted () {
this.getData()
},
watch: {
startTimeView (newValue, oldValue) {
this.getData()
},
endTimeView (newValue, oldValue) {
this.getData()
},
status (newValue, oldValue) {
this.getData()
}
},
methods: {
getData () {
let params = new URLSearchParams()
var self = this
if (self.startTimeView !== '') {
params.append('start_time', self.startTimeView)
}
if (self.endTimeView !== '') {
params.append('end_time', self.endTimeView)
}
if (self.status !== '') {
params.append('status', self.status)
}

var instance = axios.create({ baseURL: util.baseURL })

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('buyer/center/myBillList', params).then(res => {
if (res.data) {
self.list = res.data.data
}
})
},
onItemClick (index) {

var self = this
if (index === 0) {
self.status = ''
} else {
self.status = index
}
},
startBtnCancel () {

this.showStartPopup = false
},
startBtnOK () {

var self=this
if (self.startTimeView === '') {
var time=new Date().Format('yyyy-MM-dd')
self.startTimeView = time
}
self.showStartPopup = false
self.startTime = self.startTimeView
},
endBtnCancel () {

this.showEndPopup = false
},
endBtnOK () {

var self=this
if (self.endTimeView === '') {
var time=new Date().Format('yyyy-MM-dd')
self.endTimeView = time
}
self.showEndPopup = false
self.endTime = self.endTimeView
}
}
}



